import Preact, { Component } from "preact";
import { connect } from "react-redux";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTrashAlt, faCopy } from "@fortawesome/free-regular-svg-icons";

import {
  getPosts,
  toggleForm,
  addPost,
  removePost
} from "../../services/Posts/actions";

import Post from "../Post";
import "./posts.css";

const mapStateToProps = state => ({
  posts: state.posts.posts,
  fetching: state.posts.fetching
});

class Posts extends Component {
  state = { pageIndex: 0 };
  postsPerPage = 10;

  componentWillMount() {
    if (!this.props.posts.length) {
      this.props.getPosts();
    }
  }

  filterPosts() {
    const { pageIndex } = this.state;
    const { posts } = this.props;
    const startIndex = pageIndex * this.postsPerPage;

    return posts.slice(startIndex, startIndex + this.postsPerPage);
  }

  toNewPage(dir) {
    const { pageIndex } = this.state;
    const { posts } = this.props;

    if (
      (dir === "prev" && pageIndex === 0) ||
      (dir === "next" && this.postsPerPage * (pageIndex + 1) >= posts.length)
    ) {
      return;
    }

    this.setState(
      state =>
        dir === "next" ? (state.pageIndex += 1) : (state.pageIndex -= 1)
    );
  }

  render() {
    return (
      <div className="posts">
        {this.props.fetching && <p>Loading...</p>}
        {this.filterPosts().map(post => (
          <div className="posts__post-container">
            <Post post={post} />
            <a
              href=""
              className="posts__btn"
              onClick={evt => {
                evt.preventDefault();
                this.props.removePost(post.id);
              }}
            >
              <FontAwesomeIcon
                icon={faTrashAlt}
                color="#ff0000"
                className="posts__btn-icon"
              />
            </a>
            <a
              href=""
              className="posts__btn"
              onClick={evt => {
                evt.preventDefault();
                this.props.addPost({ ...post, id: null });
              }}
            >
              <FontAwesomeIcon icon={faCopy} className="posts__btn-icon" />
            </a>
          </div>
        ))}
        <button className="btn posts__add-btn" onClick={this.props.toggleForm}>
          +
        </button>
        <div className="posts__overlay" />
      </div>
    );
  }
}

export default connect(
  mapStateToProps,
  { getPosts, removePost, toggleForm, addPost }
)(Posts);



